import React from "react";
import { withStateHandlers, branch, renderComponent, compose } from "recompose";

import Layout from "components/layout";
import Hero from "components/partners/accounting/hero";
import ValueProps from "components/partners/accounting/valueProps";
import Testimonials from "components/partners/accounting/testimonials";
import Comparison from "components/partners/accounting/comparison";
import SignUp from "components/partners/accounting/signup";
import BlogLinks from "components/partners/accounting/blogLinks";
import ThankYou from "components/partners/accounting/thankyou";

const _Content = ({ setHasSignedUp }) => (
  <React.Fragment>
    <Hero />
    <ValueProps />
    <Testimonials />
    <SignUp setHasSignedUp={setHasSignedUp} />
    <Comparison />
    <BlogLinks />
  </React.Fragment>
);

const Content = compose(
  withStateHandlers(
    { hasSignedUp: false },
    { setHasSignedUp: () => (value) => ({ hasSignedUp: value }) },
  ),
  branch(
    ({ hasSignedUp }) => !!hasSignedUp,
    renderComponent(({ setHasSignedUp }) => (
      <ThankYou setHasSignedUp={setHasSignedUp} />
    )),
  ),
)(_Content);

const Accounting = ({ location }) => (
  <Layout
    location={location}
    whiteContentBackground
    title="Join the Brex Partner Program | Brex"
    description="Become a Brex Partner today & enjoy saving time, pleasing your current clients, & growing your client base. Find out more & join now!"
  >
    <Content />
  </Layout>
);

export default Accounting;
