const regularPadding = 20;
const extraPadding = 20;
const minHeight = 90;

export const BreakdownCell = styled("div")(
  ({
    isHeader = false,
    isLabel = false,
    isHighlighted = false,
    isExtendedTop = false,
    isExtendedBottom = false,
    isSectionTitle = false,
    index = false,
    noBottomBorder = false,
    trailing = false,
    subHeader = false,
  }) => [
    {
      overflow: "hidden",
      display: "flex",
      width: "20%",
      flexGrow: 0,
      flexShrink: 0,
      background: colors.black,
      color: colors.white,
      alignItems: "center",
      justifyContent: "center",
      textAlign: "center",
      fontSize: 14,
      ...padding(regularPadding),
      fontWeight: 500,
    },
    isHeader && {
      minHeight,
      fontWeight: 600,
      color: isHighlighted ? colors.white : colors.primary,
      fontSize: 32,
      paddingBottom: 0,
      [breakPoints.medium]: {
        fontSize: 26,
      },
    },
    {
      borderBottom: !(isHeader || isHighlighted)
        ? `1px solid ${colors.darkGray}`
        : "1px solid transparent",
    },
    isHighlighted && {
      background: colors.primary,
    },
    isExtendedTop && {
      minHeight: minHeight + extraPadding,
      paddingTop: regularPadding + extraPadding,
    },
    isExtendedBottom && {
      paddingBottom: regularPadding + extraPadding,
      boxShadow: "0 3px 5px rgba(0, 0, 0, .1)",
    },
    index && {
      width: "8%",
      borderBottom: "1px solid transparent",
      justifyContent: "flex-end",
      color: colors.duskyGray,
      fontWeight: 600,
      fontSize: 12,
    },
    isLabel && {
      width: "30%",
      color: colors.primary,
      fontWeight: 500,
      justifyContent: "flex-start",
      fontSize: 16,
      textAlign: "left",
    },
    isSectionTitle && {
      width: "70%",
      fontSize: 22,
      lineHeight: "32px",
      fontWeight: 500,
      color: colors.white,
      justifyContent: "flex-start",
    },
    noBottomBorder && {
      borderBottom: "none",
    },
    trailing && {
      width: "2%",
      borderBottom: "none",
    },
    subHeader && {
      fontSize: 16,
      color: colors.white,
      borderBottom: "none",
      paddingTop: 0,
    },
  ],
);

